import unitag from '../../components/uni-tag/uni-tag.vue';
import uniSection from '../../components/uni-section/uni-section.vue';
import { request } from '../../utils/request.js';
export default {
	components: { unitag, uniSection },
	data() {
		return {
			// 总收益
			all: '0',
			// 前日收益
			lasmo: '0',
			// 昨日收益
			yester: '0',
			// 可提现金额
			wimoney: '0',
			// 提现模态框显示隐藏
			showMonmodal: false,
			// 有无提现记录 0有 1无
			haslist: 1,
			// token
			token: '',
			// 要提现的金额
			form: {
				cashMoney: ''
			},
			// 提现记录
			widthdrawList: [],
			// 提现记录页码
			page: 1,
			// 是否有上一页
			hasprepage: true,
			// 是否有下一页
			hasNextpage: true,
			// 添加银行卡信息模态框的显示隐藏
			enroll: false,
			// 待添加的银行卡相关真实信息
			enrollForm: {}
		};
	},
	// 设置分享信息
	onShareAppMessage(res) {
		let pid = JSON.parse(uni.getStorageSync('login')).user_id;
		return {
			title: '分享标题',
			path: `/pages/anchor/anchor?pid=${pid}`,
			imgUrl: 'https://ww1.yunjiexi.club/2020/03/18/GwFBk.png'
		};
	},
	mounted() {
		this.getToken();
		// 调用获取收益信息的函数
		this.getWithdraw();
		// 获取经纪人提现记录
		this.getWithdrawList();
	},
	methods: {
		// 登记信息模态框的显示
		enrollCard() {
			this.enroll = true;
		},
		// 提交银行卡信息
		enrollInfo() {
			const that = this;
			request({
				url: '/api/user/withdrawInfo',
				data: this.enrollForm,
				method: 'POST'
			}).then(res => {
					if (res.data.code == 401) {
						uni.reLaunch({
							url: '/pages/authorise/authorise'
						});
					}else if (res.data.code == 1) {
						uni.showToast({
							title: '信息登记成功，您可以继续提现',
							icon: 'success'
						});
						this.enrollForm = {};
						this.enroll = false;
					} else {
						uni.showToast({
							title: res.data.msg,
							icon: 'none'
						});
						this.enrollForm = {};
						this.enroll = false;
					}
				})
				.catch(err => {
				});
		},
		// 获取经纪人提现记录
		getWithdrawList() {
			const that = this;
			request({
				url: '/api/user/withdrawList',
				data: { page: that.page }
			}).then(res => {
				if (res.data.code == 401) {
					uni.reLaunch({
						url: '/pages/authorise/authorise'
					});
					return
				}
				if (res.data.data.lists.length != 0) {
					that.haslist = 0;
				}
				// 修改返回的时间格式
				res.data.data.lists.map(item => {
					item.createtime_text = item.createtime_text.slice(0, 10);
				});
				that.widthdrawList = res.data.data.lists;
				// 判断是否有下一页上一页
				if (res.data.data.has_next == 0) {
					this.hasNextpage = true;
				} else {
					this.hasNextpage = false;
				}
				if (this.page > 1) {
					this.hasprepage = false;
				} else {
					this.hasprepage = true;
				}
			});
		},
		// 跳转上一页列表
		getPrepage() {
			this.page = this.page - 1;
			this.getWithdrawList();
		},
		// 下一页列表
		getNextpage() {
			this.page = this.page + 1;
			this.getWithdrawList();
		},
		// 获取token
		getToken() {
			this.token = uni.getStorageSync('token');
		},
		getWithdraw() {
			// 获取收益信息，更新数据，绑定数据
			const that = this;
			request({
				url: '/api/user/cashShow',
				method: 'POST'
			}).then(res => {
				if (res.data.code == 401) {
					uni.reLaunch({
						url: '/pages/authorise/authorise'
					});
				}
				that.all = res.data.data.total_money;
				that.lasmo = res.data.data.before_yester_money;
				that.yester = res.data.data.yester_money;
				that.wimoney = res.data.data.able_money;
			});
		},
		// /提现模态框的显示隐藏
		getMoney() {
			this.showMonmodal = true;
		},
		closeModal() {
			this.showMonmodal = false;
		},
		closeModal1() {
			this.enroll = false;
		},
		// 关于模态框的函数
		withdraw() {
			const that = this;
			if (that.form.cashMoney <= that.wimoney) {
				request({
					url: '/api/user/applyWithdraw',
					method: 'POST',
					data: {
						money: that.form.cashMoney
					}
				}).then(res => {
					if (res.data.code == 401) {
						uni.reLaunch({
							url: '/pages/authorise/authorise'
						});
						return
					}
					if (res.code !== 1) {
						uni.showToast({
							title: res.data.msg,
							icon: 'none'
						});
						that.form.cashMoney = '';
						this.getWithdraw();
						this.getWithdrawList();
					} else {
						that.form.cashMoney = '';
						uni.showToast({
							title: res.data.msg,
							icon: 'success'
						});
						this.getWithdraw();
						this.getWithdrawList();
					}
				});
			} else {
				uni.showToast({
					title: '超出可提现金额，请重新填写',
					icon: 'none'
				});
				setTimeout(() => {
					this.showMonmodal = true;
				}, 2000);
			}
		}
	}
};
